.icon {
  display: flex;
}
.countryList {
  display: flex;
  flex-wrap: wrap;
}

img {
  width: 20vh;
  height: 20vh;
  border-radius: 0.3rem;
  box-shadow: 0 0 0.6rem 0.2rem rgb(241, 225, 225);
}


b {
  font-size: 16px;
}
input {
  color: #ffa500;
  width: 50%;
  height: 5rem;
  margin: 3rem auto;
  text-indent: 2rem;
  outline: none;
  border: 0.2rem solid rgb(250, 246, 246);
  font-size: 2rem;
  border-radius: 3rem;
  background: rgb(250, 246, 246);
}

.controls {
  width: 100%;
  text-align: center;
}
.header {
  width: 100%;
}

p {
  font-size: 16px;
  font-weight: bolder;
}
b {
  color: black;
  font-size: 22px;
}
.sortDiv {
  display: flex;
  justify-content: space-evenly;
}

.sortDiv button {
  border-radius: 20px;
  margin-bottom: 20px;
}
